https://prismjs.com/download.html#themes=prism&languages=markup+css+clike+javascript+css-extras+diff+javadoclike+jsdoc+json+jsx+tsx+typescript&plugins=line-highlight+line-numbers+autolinker+file-highlight+inline-color+command-line+diff-highlight+treeview */
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

 code[class*="language-"],
 pre[class*="language-"] {
	 color: var(--text-color);
	 background: none;
	 text-shadow: 0 1px var(--background-color);
	 font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	 font-size: 16px;
	 text-align: left;
	 white-space: pre;
	 word-spacing: normal;
	 word-break: normal;
	 word-wrap: normal;
	 line-height: 1.5;
 
	 -moz-tab-size: 4;
	 -o-tab-size: 4;
	 tab-size: 4;
 
	 -webkit-hyphens: none;
	 -moz-hyphens: none;
	 -ms-hyphens: none;
	 hyphens: none;
 }
 
 pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
 code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
	 text-shadow: none;
	 background: #0912da57;
 }
 
 pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
 code[class*="language-"]::selection, code[class*="language-"] ::selection {
	 text-shadow: none;
	 background: #0912da57;
 }
 
 @media print {
	 code[class*="language-"],
	 pre[class*="language-"] {
		 text-shadow: none;
	 }
 }
 
 /* Code blocks */
 pre[class*="language-"] {
	 padding: 1em;
	 margin: .5em 0;
	 overflow: auto;
	 margin-left: -20px;
	 margin-right: -20px;
	 border-radius: 10px;
 }
 
 :not(pre) > code[class*="language-"],
 pre[class*="language-"] {
	 background: var(--code-background-color);
 }
 
 /* Inline code */
 :not(pre) > code[class*="language-"] {
	 padding: .1em;
	 border-radius: .3em;
	 white-space: normal;
 }
 
 .token.comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
	 color: slategray;
 }
 
 .token.punctuation {
	 color: #999;
 }
 
 .token.namespace {
	 opacity: .7;
 }
 
 .token.property,
 .token.tag,
 .token.boolean,
 .token.number,
 .token.constant,
 .token.symbol,
 .token.deleted {
	 color: #905;
 }
 
 .token.selector,
 .token.attr-name,
 .token.string,
 .token.char,
 .token.builtin,
 .token.inserted {
	 color: #690;
 }
 
 .token.operator,
 .token.entity,
 .token.url,
 .language-css .token.string,
 .style .token.string {
	 color: #9a6e3a;
	 /* This background color was intended by the author of this theme. */
	 background: var(--code-outline-color);
 }
 
 .token.atrule,
 .token.attr-value,
 .token.keyword {
	 color: #07a;
 }
 
 .token.function,
 .token.class-name {
	 color: #DD4A68;
 }
 
 .token.regex,
 .token.important,
 .token.variable {
	 color: #e90;
 }
 
 .token.important,
 .token.bold {
	 font-weight: bold;
 }
 .token.italic {
	 font-style: italic;
 }
 
 .token.entity {
	 cursor: help;
 }
 
 pre[data-line] {
	 position: relative;
	 padding: 1em 0 1em 3em;
 }
 
 .line-highlight {
	 position: absolute;
	 left: 0;
	 right: 0;
	 padding: inherit 0;
	 margin-top: 1em; /* Same as .prism’s padding-top */
 
	 background: hsla(24, 20%, 50%,.08);
	 background: linear-gradient(to right, hsla(290deg 68% 39% / 13%) 70%, hsla(24, 20%, 50%,0));
 
	 border-left: 3px solid #af69d3;

	 pointer-events: none;
 
	 line-height: inherit;
	 white-space: pre;
 }
 
 @media print {
	 .line-highlight {
		 /*
		  * This will prevent browsers from replacing the background color with white.
		  * It's necessary because the element is layered on top of the displayed code.
		  */
		 -webkit-print-color-adjust: exact;
		 color-adjust: exact;
	 }
 }
 
	 .line-highlight:before,
	 .line-highlight[data-end]:after {
		 content: attr(data-start);
		 position: absolute;
		 top: .4em;
		 left: .6em;
		 min-width: 1em;
		 padding: 0 .5em;
		 background-color: hsla(24, 20%, 50%,.4);
		 color: hsl(24, 20%, 95%);
		 font: bold 65%/1.5 sans-serif;
		 text-align: center;
		 vertical-align: .3em;
		 border-radius: 999px;
		 text-shadow: none;
		 box-shadow: 0 1px var(--background-color);
	 }
 
	 .line-highlight[data-end]:after {
		 content: attr(data-end);
		 top: auto;
		 bottom: .4em;
	 }
 
 .line-numbers .line-highlight:before,
 .line-numbers .line-highlight:after {
	 content: none;
 }
 
 pre[id].linkable-line-numbers span.line-numbers-rows {
	 pointer-events: all;
 }
 pre[id].linkable-line-numbers span.line-numbers-rows > span:before {
	 cursor: pointer;
 }
 pre[id].linkable-line-numbers span.line-numbers-rows > span:hover:before {
	 background-color: rgba(128, 128, 128, .2);
 }
 
 pre[class*="language-"].line-numbers {
	 position: relative;
	 padding-left: 50px;
	 counter-reset: linenumber;
 }
 
 pre[class*="language-"].line-numbers > code {
	 position: relative;
	 white-space: inherit;
 }

.line-numbers .line-numbers-rows {
	position: absolute;
	pointer-events: none;
	top: 0;
	font-size: 100%;
	left: -50px;
	/* width: 3em; /* works for line-numbers below 1000 lines */
	letter-spacing: -1px;
	padding-left: 8px;
	/* border-right: 1px solid #999; */

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}

	.line-numbers-rows > span {
		display: block;
		counter-increment: linenumber;
	}

		.line-numbers-rows > span:before {
			content: counter(linenumber);
			color: #8e8ebc;
			display: block;
			padding-right: 0.8em;
			text-align: right;
			font-size: 14px;
			line-height: 24px;
		}

.token a {
	color: inherit;
}
span.inline-color-wrapper {
	/*
	 * The background image is the following SVG inline in base 64:
	 *
	 * <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 2 2">
	 *     <path fill="gray" d="M0 0h2v2H0z"/>
	 *     <path fill="white" d="M0 0h1v1H0zM1 1h1v1H1z"/>
	 * </svg>
	 *
	 * SVG-inlining explained:
	 * https://stackoverflow.com/a/21626701/7595472
	 */
	background: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyIDIiPjxwYXRoIGZpbGw9ImdyYXkiIGQ9Ik0wIDBoMnYySDB6Ii8+PHBhdGggZmlsbD0id2hpdGUiIGQ9Ik0wIDBoMXYxSDB6TTEgMWgxdjFIMXoiLz48L3N2Zz4=");
	/* This is to prevent visual glitches where one pixel from the repeating pattern could be seen. */
	background-position: center;
	background-size: 110%;

	display: inline-block;
	height: 1.333ch;
	width: 1.333ch;
	margin: 0 .333ch;
	box-sizing: border-box;
	border: 1px solid white;
	outline: 1px solid rgba(0,0,0,.5);
	overflow: hidden;
}

span.inline-color {
	display: block;
	/* To prevent visual glitches again */
	height: 120%;
	width: 120%;
}

.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.command-line-prompt > span:before {
	color: #999;
	content: ' ';
	display: block;
	padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
	content: attr(data-prompt);
}

pre.diff-highlight > code .token.deleted:not(.prefix),
pre > code.diff-highlight .token.deleted:not(.prefix) {
	background-color: rgba(255, 0, 0, .1);
	color: inherit;
	display: block;
}

pre.diff-highlight > code .token.inserted:not(.prefix),
pre > code.diff-highlight .token.inserted:not(.prefix) {
	background-color: rgba(0, 255, 128, .1);
	color: inherit;
	display: block;
}

.token.deleted {
	background-color: #ffe1e1;

	html[data-theme="dark"] & {
    background-color: #8d2d2d3b;
  }
}

.token.inserted {
	background-color: #f0ffde;

	html[data-theme="dark"] & {
    background-color: #84b14e12;
  }
}
